import { ENTER_USERNAME, ENTER_PASSWORD, SET_TOKEN } from "./Types";
import $ from 'jquery'

export const authorize = () => dispatch => {
    $.ajax(
        'http://emphasoft-test-assignment.herokuapp.com/api-token-auth/',
        {
            type: 'POST',
            data: JSON.stringify({
                username: 'test_super',
                password: 'Nf<U4f<rDbtDxAPn'
            }),
            headers: {
                'content-type': 'application/json'
            },
            success: data => {
                dispatch({
                    type: SET_TOKEN,
                    payload: data.token
                })
            }
        }
    )
};